'use client'

import { useState } from 'react'
import Image from 'next/image'
import Link from 'next/link'
import { Heart, Play } from 'lucide-react'
import { Anime } from '@/lib/api'

interface AnimeCardProps {
  anime: Anime
}

export function AnimeCard({ anime }: AnimeCardProps) {
  const [isFavorite, setIsFavorite] = useState(false)

  return (
    <Link href={`/anime/${anime.mal_id}`} className="block">
      <div className="group relative cursor-pointer rounded-lg overflow-hidden shadow-lg hover:shadow-2xl transition-shadow duration-300 h-full flex flex-col">
        
        {/* Poster Image */}
        <div className="relative w-full h-72 sm:h-80">
          <Image
            src={anime.images.jpg.large_image_url || "/placeholder.svg"}
            alt={anime.title}
            fill
            className="object-cover group-hover:scale-105 transition-transform duration-300"
          />

          {/* Gradient Overlay Bawah */}
          <div className="absolute bottom-0 left-0 right-0 h-1/3 bg-gradient-to-t from-black/70 to-transparent opacity-0 group-hover:opacity-100 transition-opacity"></div>

          {/* Play Button */}
          <div className="absolute inset-0 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity">
            <div className="w-14 h-14 bg-black/60 rounded-full flex items-center justify-center">
              <Play size={24} className="text-white" />
            </div>
          </div>

          {/* Score Badge */}
          {anime.score != null && (
            <div className="absolute top-2 right-2 bg-white/90 px-3 py-1 rounded-full text-sm font-bold text-black">
              {anime.score.toFixed(1)}
            </div>
          )}
        </div>

        {/* Favorite Button */}
        <button
          onClick={(e) => {
            e.preventDefault()
            setIsFavorite(!isFavorite)
          }}
          className="absolute top-2 left-2 z-10 p-2 bg-white/90 rounded-full border border-gray-300 hover:bg-red-500 hover:text-white transition-colors"
        >
          <Heart
            size={18}
            className={isFavorite ? 'fill-current text-red-500' : 'text-gray-700'}
          />
        </button>

        {/* Content */}
        <div className="p-3 bg-card flex flex-col flex-1">
          <h3 className="font-semibold text-sm line-clamp-2 text-foreground group-hover:text-primary transition-colors">
            {anime.title}
          </h3>
          <p className="text-xs text-muted-foreground mt-1">
            {anime.episodes ?? 'TBA'} Episodes
          </p>
          {anime.genres && anime.genres.length > 0 && (
            <p className="text-xs text-muted-foreground mt-auto">
              {anime.genres.slice(0, 2).map(g => g.name).join(', ')}
            </p>
          )}
        </div>
      </div>
    </Link>
  )
}
