'use client'

import { useState, useEffect, useRef } from 'react'
import Link from 'next/link'
import { useRouter } from 'next/navigation'
import { Search, Menu, X } from 'lucide-react'
import { Anime, searchAnime } from '@/lib/api'
import { ThemeToggle } from '@/components/theme-toggle'

export function Navbar() {
  const [isOpen, setIsOpen] = useState(false)
  const [searchQuery, setSearchQuery] = useState('')
  const [results, setResults] = useState<Anime[]>([])
  const [loading, setLoading] = useState(false)
  const router = useRouter()
  const searchRef = useRef<HTMLDivElement>(null)

  // Debounce timer
  const debounceRef = useRef<NodeJS.Timeout | null>(null)

  useEffect(() => {
    if (!searchQuery) {
      setResults([])
      setLoading(false)
      return
    }

    setLoading(true)
    if (debounceRef.current) clearTimeout(debounceRef.current)

    debounceRef.current = setTimeout(async () => {
      try {
        const data = await searchAnime(searchQuery)
        setResults(data.data)
      } catch (err) {
        console.error(err)
        setResults([])
      } finally {
        setLoading(false)
      }
    }, 400) // 400ms delay
  }, [searchQuery])

  // Close search dropdown when clicked outside
  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (searchRef.current && !searchRef.current.contains(event.target as Node)) {
        setResults([])
      }
    }
    document.addEventListener('mousedown', handleClickOutside)
    return () => document.removeEventListener('mousedown', handleClickOutside)
  }, [])

  return (
    <nav className="sticky top-0 z-50 bg-card/95 backdrop-blur border-b border-border">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16">
          {/* Logo */}
          <Link href="/" className="flex items-center gap-2 group">
            <div className="w-8 h-8 bg-primary rounded-lg flex items-center justify-center neon-glow">
              <span className="text-white font-bold text-lg">A</span>
            </div>
            <span className="text-xl font-bold text-foreground hidden sm:inline group-hover:text-primary transition-colors">
              Stream4nzu
            </span>
          </Link>

          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center gap-8">
            <Link href="/" className="text-foreground hover:text-primary transition-colors text-sm font-medium">Home</Link>
            <Link href="/popular" className="text-foreground hover:text-primary transition-colors text-sm font-medium">Popular</Link>
            <Link href="/trending" className="text-foreground hover:text-primary transition-colors text-sm font-medium">Trending</Link>
            <Link href="/genres" className="text-foreground hover:text-primary transition-colors text-sm font-medium">Genres</Link>
          </div>

          {/* Search Bar */}
          <div className="hidden sm:flex relative flex-1 max-w-xs" ref={searchRef}>
            <div className="flex items-center gap-2 bg-input border border-border rounded-lg px-3 py-2 w-full">
              <Search size={18} className="text-muted-foreground" />
              <input
                type="text"
                placeholder="Search anime..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="bg-transparent outline-none text-sm flex-1 placeholder:text-muted-foreground"
              />
            </div>

            {/* Live Search Dropdown */}
            {searchQuery && results.length > 0 && (
              <div className="absolute top-full mt-2 w-full bg-card border border-border rounded-lg shadow-lg max-h-80 overflow-y-auto z-50">
                {results.map((anime) => (
                  <Link
                    key={anime.mal_id}
                    href={`/anime/${anime.mal_id}`}
                    onClick={() => setResults([])}
                    className="flex items-center gap-2 px-3 py-2 hover:bg-primary/10 transition-colors"
                  >
                    <img
                      src={anime.images.jpg.small_image_url || '/placeholder.svg'}
                      alt={anime.title}
                      className="w-10 h-14 object-cover rounded"
                    />
                    <div className="flex-1">
                      <p className="text-sm font-medium text-foreground line-clamp-1">{anime.title}</p>
                      <p className="text-xs text-muted-foreground">{anime.episodes ?? 'TBA'} Episodes</p>
                    </div>
                  </Link>
                ))}
              </div>
            )}

            {searchQuery && results.length === 0 && !loading && (
              <div className="absolute top-full mt-2 w-full bg-card border border-border rounded-lg shadow-lg px-3 py-2 text-sm text-muted-foreground">
                No results found
              </div>
            )}
          </div>

          <div className="flex items-center gap-2">
            <ThemeToggle />
            {/* Mobile Menu Button */}
            <button
              className="md:hidden p-2 hover:bg-secondary rounded-lg transition-colors"
              onClick={() => setIsOpen(!isOpen)}
            >
              {isOpen ? <X size={24} /> : <Menu size={24} />}
            </button>
          </div>
        </div>

        {/* Mobile Menu */}
        {isOpen && (
          <div className="md:hidden pb-4 border-t border-border space-y-2">
            <Link href="/" className="block py-2 text-foreground hover:text-primary transition-colors">Home</Link>
            <Link href="/popular" className="block py-2 text-foreground hover:text-primary transition-colors">Popular</Link>
            <Link href="/trending" className="block py-2 text-foreground hover:text-primary transition-colors">Trending</Link>
            <Link href="/genres" className="block py-2 text-foreground hover:text-primary transition-colors">Genres</Link>
            {/* Mobile Search */}
            <div className="mt-2 flex items-center gap-2 bg-input border border-border rounded-lg px-3 py-2">
              <Search size={18} className="text-muted-foreground" />
              <input
                type="text"
                placeholder="Search anime..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="bg-transparent outline-none text-sm flex-1 placeholder:text-muted-foreground"
              />
            </div>
          </div>
        )}
      </div>
    </nav>
  )
}
